@import '~@angular/material/theming';
@include mat-core();
//Pallettes generated on http://mcg.mbitson.com/

:root {
    --eSolve-primary: #1078CF;
    --eSolve-secondary: #EEEEF1;
    --eSolve-white: #FFFFFF;
    --eSolve-blue:    #1078CF;
    --eSolve-indigo:  #4E0B9B;
    --eSolve-purple:  #380F66;
    --eSolve-pink:    #AD2A81;
    --eSolve-red:     #DC3545;
    --eSolve-orange:  #DD7529;
    --eSolve-yellow:  #F2A900;
    --eSolve-green:   #458045;
    --eSolve-teal:    #157A80;
    --eSolve-cyan:    #00FFFF;
    --eSolve-gray: #D5D6E0;
    --eSolve-gray-dark: #161616;
    --eSolve-tile: #c1d1ef;
}

$eSolve-blue: (
    50 : #e6e7e9,
    100 : #bfc2c8,
    200 : #959aa4,
    300 : #6b7180,
    400 : #4b5264,
    500 : #2b3449,
    600 : #262f42,
    700 : #202739,
    800 : #1a2131,
    900 : #101521,
    A100 : #1078CF,
    A200 : #3060ff,
    A400 : #003afc,
    A700 : #0034e3,
    contrast: (
        50 : #000000,
        100 : #000000,
        200 : #000000,
        300 : #ffffff,
        400 : #ffffff,
        500 : #ffffff,
        600 : #ffffff,
        700 : #ffffff,
        800 : #ffffff,
        900 : #ffffff,
        A100 : #000000,
        A200 : #ffffff,
        A400 : #ffffff,
        A700 : #ffffff,
    )
);


$eSolve-primary: mat-palette($eSolve-blue, A100);
$eSolve-accent: mat-palette($eSolve-blue, A100);
$eSolve-warn: mat-palette($mat-red);

$eSolve-theme: mat-light-theme($eSolve-primary, $eSolve-accent, $eSolve-warn);

@include angular-material-theme($eSolve-theme);
