/////////////////global mat paginator styling/////////////////////////
.mat-paginator {
  .mat-paginator-outer-container {
    padding: 0px;
    .mat-paginator-range-label {
      margin: 0 5px 0 5px;
    font-size: 10px;
    padding-top: 1px;
    }
    .mat-paginator-container {
      .mat-paginator-page-size {
        margin: 0;
        .mat-paginator-page-size-label {
          font-size: 10px;
        }
        .mat-paginator-page-size-select {
          .mat-form-field-wrapper {
            .mat-form-field-flex {
              .mat-form-field-infix {
                .mat-select {
                  .mat-select-trigger {
                    font-size: 10px;
                    width: 37px;
                    .mat-select-value {
                      span {
                        font-size: 10px;
                      }
                    }
                  }
                }
              }
            }
            .mat-form-field-underline {
              width: 37px;
            }
          }
        }
      }
      .mat-paginator-range-actions {
        .btn_custom-paging-container {
          overflow: hidden;
          .mat-icon-button {
            width: 35px;
            height: 16px;
          }
        }
      }
      .btn_custom-paging-container {
        .mat-icon-button {
          line-height: 16px;
        }
      }
    }
  }
}
